, 255, 255, 0.1);\r\n  display: block;\r\n  transition: 0.4s;\r\n  text-decoration: none;\r\n}\r\n.sidebar #side li {\r\n  position: relative;\r\n  font-size: 20px;\r\n  padding: 10px 0;\r\n}\r\n.sidebar #side li a {\r\n  color: black;\r\n  min-width: 44px;\r\n  min-height: 44px;\r\n  display: block;\r\n}\r\n.sidebar #side li a::after {\r\n  content: \"\";\r\n  position: absolute;\r\n  bottom: 0;\r\n  left: 50%;\r\n  width: 0;\r\n  height: 1.5px;\r\n  transform: translateX(-50%);\r\n  background-color: #000000;\r\n  transition: width 0.3s ease;\r\n}\r\n.sidebar #side li:hover a::after {\r\n  width: 5rem;\r\n}\r\n#check {\r\n  display: none;\r\n}\r\nlabel #btn,\r\nlabel #cancel {\r\n  position: fixed;\r\n  cursor: pointer;\r\n  z-index: 110;\r\n}\r\nlabel #btn {\r\n  left: 25px;\r\n  top: 0.7rem;\r\n  font-size: 30px;\r\n  color: black;\r\n  padding: 5px 10px;\r\n  transition: all 0.5s;\r\n  position: fixed;\r\n  z-index: 100;\r\n}\r\n\r\nlabel #cancel {\r\n  left: -195px;\r\n  top: 17px;\r\n  font-size: 30px;\r\n  color: #000000;\r\n  padding: 4px 9px;\r\n  transition: all 0.5s ease;\r\n}\r\n#check:checked ~ .sidebar {\r\n  left: 0;\r\n}\r\n#check:checked ~ label #btn {\r\n  left: 250px;\r\n  opacity: 0;\r\n  pointer-events: none;\r\n}\r\n#check:checked ~ label #cancel {\r\n  left: 195px;\r\n}\r\n#btn {\r\n  display: none;\r\n}\r\n#judul {\r\n  font-weight: bolder;\r\n}\r\n/*responsive sidebar and tulisan front end dev*/\r\n@media (max-width: 575px) {\r\n  #check:checked ~ .sidebar {\r\n    left: 0;\r\n  }\r\n  #check:checked ~ label #btn {\r\n    left: 250px;\r\n    opacity: 0;\r\n    pointer-events: none;\r\n  }\r\n  #check:checked ~ label #cancel {\r\n    left: 195px;\r\n  }\r\n  #navbar li {\r\n    display: none;\r\n  }\r\n  #nav-btn {\r\n    display: none;\r\n  }\r\n\r\n  nav #judul {\r\n    position: absolute;\r\n    right: 10%;\r\n    font-size: 18px;\r\n  }\r\n  label #btn, label #cancel {\r\n    display: block;\r\n  }\r\n  .contact-header,\r\n  .service,\r\n  .judul {\r\n    font-size: 13px;\r\n  }\r\n  .sidebar #side li a {\r\n    height: 44px;\r\n  }\r\n}\r\n\r\n/* header and navbar*/\r\n\r\nnav ul li {\r\n  list-style: none;\r\n  margin: 5px 20px 0 20px;\r\n  position: relative;\r\n}\r\n#navbar {\r\n  display: flex;\r\n}\r\nheader {\r\n  width: 100%;\r\n  position: fixed;\r\n  background-color: white;\r\n  z-index: 99;\r\n}\r\nnav {\r\n  top: 0;\r\n  right: 0;\r\n  align-items: center;\r\n  justify-content: space-between;\r\n  transition: all 0.5s ease;\r\n  display: flex;\r\n  margin: auto;\r\n  width: 90%;\r\n  height: 55px;\r\n  line-height: normal;\r\n  padding-left: 18px;\r\n}\r\n\r\nnav ul li::after {\r\n  content: \"\";\r\n  position: absolute;\r\n  bottom: 0;\r\n  left: 0;\r\n  height: 1.5px;\r\n  background: black;\r\n  transition: all 0.3s;\r\n  width: 0;\r\n}\r\n\r\nnav ul li:hover::after {\r\n  width: 100%;\r\n}\r\n\r\nnav ul li a {\r\n  color: var(--header);\r\n  min-width: 44px;\r\n  min-height: 44px;\r\n  display: block;\r\n  line-height: 44px;\r\n}\r\n\r\nspan {\r\n  color: #fa0;\r\n}\r\n\r\n/* landing page */\r\n.moto {\r\n  padding-top: 20%;\r\n  color: white;\r\n  font-size: 20px;\r\n}\r\n.moto-container {\r\n  width: 80%;\r\n  margin: auto;\r\n}\r\n.hero {\r\n  width: 100%;\r\n  height: 75vh;\r\n  padding-top: 60px;\r\n  position: relative;\r\n  background: url(../public/hero/hero-image-large.jpg) no-repeat;\r\n  background-size: cover;\r\n}\r\n\r\n@media screen and (min-width: 1200px) {\r\n  .hero {\r\n    min-width: 1000px;\r\n  }\r\n}\r\n#moto {\r\n  background-color: rgba(255, 170, 0, 0.8);\r\n  color: white;\r\n  padding: 0 30px 0 3px;\r\n}\r\n@media screen and (max-width: 1025px) {\r\n  .moto {\r\n    font-size: 15px;\r\n    padding-top: 5rem;\r\n  }\r\n  .hero {\r\n    height: 40vh;\r\n  }\r\n}\r\n@media screen and (max-width: 500px) {\r\n  .moto {\r\n    font-size: 12px;\r\n  }\r\n}\r\n@media screen and (max-width: 780px) {\r\n  .hero {\r\n    background: url(../public/hero/hero-image-small.jpg) no-repeat;\r\n    background-size: cover;\r\n  }\r\n}\r\n\r\n/* list */\r\n#list {\r\n  color: #fa0;\r\n  font-size: 25px;\r\n  padding-top: 5rem;\r\n  margin-left: 6rem;\r\n}\r\n.list {\r\n  width: 85%;\r\n  margin: auto;\r\n}\r\n.list-cards {\r\n  display: grid;\r\n  gap: 16px;\r\n  grid-template-columns: repeat(auto-fill, minmax(150px, 240px));\r\n  place-content: center;\r\n}\r\n.card {\r\n  width: 240px;\r\n  padding: 20px;\r\n  box-shadow: 0 0 3px rgba(0, 0, 0, 0.162);\r\n  height: 270px;\r\n}\r\n\r\n.card-head {\r\n  display: flex;\r\n  justify-content: space-between;\r\n  margin-bottom: 8px;\r\n}\r\n.card-title a {\r\n  font-size: 18px;\r\n  color: #34364a;\r\n  font-weight: 700;\r\n  display: block;\r\n}\r\n.card-head p {\r\n  font-weight: 600;\r\n  color: rgb(253, 198, 88);\r\n}\r\n.card img {\r\n  width: 200px;\r\n  height: 150px;\r\n  position: relative;\r\n  z-index: 1;\r\n  margin-bottom: 6px;\r\n}\r\n\r\n@media (max-width: 500px) {\r\n  .card {\r\n    margin-top: 10px;\r\n  }\r\n  #list {\r\n    margin-left: auto;\r\n  }\r\n}\r\n/* skeleton ui */\r\n\r\n/* main page and liked skeleton */\r\n.skeleton {\r\n  width: 240px;\r\n  padding: 20px;\r\n  box-shadow: 0 0 3px rgba(0, 0, 0, 0.162);\r\n  height: 270px;\r\n}\r\n\r\n.skeleton-head {\r\n  display: flex;\r\n  justify-content: space-between;\r\n  margin-bottom: 8px;\r\n}\r\n\r\n.skeleton-line {\r\n  width: 35%;\r\n  height: 20px;\r\n  background: #e0e0e0;\r\n  border-radius: 4px;\r\n  animation: shimmer 1.5s infinite linear;\r\n  margin-bottom: 8px;\r\n}\r\n\r\n.skeleton-line.short {\r\n  width: 20%;\r\n}\r\n\r\n.skeleton-image {\r\n  width: 200px;\r\n  height: 150px;\r\n  position: relative;\r\n  z-index: 1;\r\n  margin-bottom: 6px;\r\n  animation: shimmer 1.5s infinite linear;\r\n}\r\n\r\n@media (max-width: 500px) {\r\n  .skeleton {\r\n    margin-top: 10px;\r\n  }\r\n  #list {\r\n    margin-left: auto;\r\n  }\r\n}\r\n\r\n/* detail and comment skeleton */\r\n\r\n.skeleton-detail-line {\r\n  width: 100%;\r\n  height: 24px;\r\n  background: #e0e0e0;\r\n  border-radius: 4px;\r\n  animation: shimmer 1.5s infinite linear;\r\n  margin-bottom: 8px;\r\n}\r\n.skeleton-detail-title {\r\n  width: 45%;\r\n  height: 28px;\r\n  background: #e0e0e0;\r\n  border-radius: 4px;\r\n  animation: shimmer 1.5s infinite linear;\r\n  margin-bottom: 8px;\r\n}\r\n\r\n.skeleton-detail-line-short {\r\n  width: 25%;\r\n}\r\n\r\n.skeleton-detail-image {\r\n  width: 100%;\r\n  height: 150px;\r\n  background: #e0e0e0;\r\n  border-radius: 4px;\r\n  animation: shimmer 1.5s infinite linear;\r\n  margin-bottom: 6px;\r\n}\r\n\r\n.skeleton-detail-info,\r\n.skeleton-detail-description {\r\n  margin-top: 20px;\r\n}\r\n\r\n.skeleton-detail-comments {\r\n  margin-top: 20px;\r\n}\r\n\r\n.skeleton-detail-comments-container {\r\n  display: flex;\r\n  flex-direction: column;\r\n  gap: 8px;\r\n}\r\n\r\n.skeleton-detail-comment-card {\r\n  background: white;\r\n  padding: 16px;\r\n  border: 1px solid #e0e0e0;\r\n  border-radius: 8px;\r\n}\r\n\r\n\r\n/* Shimmer effect */\r\n@keyframes shimmer {\r\n  0% {\r\n    background: linear-gradient(\r\n      90deg,\r\n      #e0e0e0 25%,\r\n      #f0f0f0 50%,\r\n      #e0e0e0 75%\r\n    );\r\n    background-size: 200% 100%;\r\n    background-position: -100% 0;\r\n  }\r\n  100% {\r\n    background-position: 100% 0;\r\n  }\r\n}\r\n\r\n\r\n/* footer */\r\nfooter {\r\n  width: 100%;\r\n  display: block;\r\n  text-align: center;\r\n  padding: 20px 0 20px 0;\r\n  position: relative;\r\n  bottom: 0;\r\n  font-size: 1rem;\r\n  min-height: 40px;\r\n}\r\n\r\n/* submission */\r\n#skip-button {\r\n  position: absolute;\r\n  top: -50px;\r\n  font-size: 22px;\r\n  left: 0;\r\n  background-color: red;\r\n  color: white;\r\n  padding: 5px;\r\n  z-index: 99999;\r\n}\r\n#skip-button:focus {\r\n  top: 0;\r\n}\r\n\r\n/* detail page */\r\n.detail {\r\n  min-width: 65%;\r\n  padding: 70px 20px 20px 20px;\r\n  margin: auto;\r\n  display: grid;\r\n  grid-template-columns: 1fr; \r\n  gri